import uniNavBar from "../uni-nav-bar/uni-nav-bar.vue"
export default {
	components: {
		uniNavBar
	},
	props: {
		navDates: Array,
		navBartSelect: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			selectPage: this.navBartSelect
		}
	},
	methods: {
		navClick(index) {
			this.$emit("navClick", index);
			this.selectPage = index;
		}
	}
}
